* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: sans-serif;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
}

main {
  background-image: radial-gradient(#E6F4F1 65%, rgba(0, 0, 0, 0) 65%);
  background-repeat: space;
  background-size: 0.5rem 0.5rem;
	min-height: 100vh;
}

main section {
  padding-top: 10rem;
  padding-left: 10em;
  padding-right: 10em;
}

main section:nth-child(1) {
  padding-top: 7rem;
}

h1 {
  font-size: 2.5em;
}

p {
  font-size: 1em;
  font-weight: bold;
}

button, .btn {
  background: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 1rem;
  border-radius: 0.7em;
  border: 1px solid;
  font-weight: 700;
}

button:hover {
  cursor:pointer;
}

.color-main {
  color: #D8A45C;
}

.color-main-2 {
  color: #9E702A ;
}

.color-secondary {
  color: #3B4856 ;
}

.color-tertiary {
  color: #78818d ;
}

.color-blue {
  color: #4db5ff;
}

.btn-style-1 {
  color: #3B4856 ;
}

.btn-style-2 {
  background: #3B4856;
  color: #E6F4F1 ;
  border: none;
}


.icon-color1 {
  -webkit-filter: invert(68%) sepia(70%) saturate(336%) hue-rotate(350deg) brightness(90%) contrast(88%);
          filter: invert(68%) sepia(70%) saturate(336%) hue-rotate(350deg) brightness(90%) contrast(88%);
}

.icon-social-hover:hover {
  -webkit-filter: invert(77%) sepia(9%) saturate(530%) hue-rotate(172deg) brightness(89%) contrast(85%);
          filter: invert(77%) sepia(9%) saturate(530%) hue-rotate(172deg) brightness(89%) contrast(85%);
}

button:hover, .btn:hover {
  background: #9fadbd;
  color: #fff ;
}

.container {
  padding-left: 1em;
  padding-right: 1em;
}

.container-row {
  display: flex;
}

.section-heading {
  width: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 0.4em;
  gap: 0.4em;
  align-items: center;
}

.buttonsContainer {
  width: 100%;
  display: flex;
  justify-content: center;
  grid-gap: 1.4em;
  gap: 1.4em;
}

@media only screen and (max-width: 950px) {
  section {
    min-height: 100vh;
  }

  main section {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}
.container-intro {
  width: 100%;
  height: 20em;
  display: flex;
  justify-content: space-between;
  padding-left: 20%;
  padding-right: 20%;
}

.owner-name {
  z-index: 2;
  margin-top: 1rem;
}

.owner-name .underline {
  background-image: radial-gradient(#3B4856 50%, rgba(0,0,0,0) 50%);
  background-repeat: space;
  background-size: 0.3rem 0.3rem;
  width: 100%;
  position: relative;
  height: 1rem;
  bottom: 0.8rem;
  left: 0.8rem;
  z-index: -1;
}

.container-intro-sections {
  width: 65%;
  height: 14em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container-intro-sections .buttonsContainer {
  margin-top: 2.4rem;
}

.scroll-down {
  margin-top: 5rem;
  height: 10%;
  display: flex;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  white-space: nowrap;
}

@media only screen and (max-width: 950px) {
  .container-intro {
    padding-left: 0;
    padding-right: 0;
    flex-direction: column;
    justify-content: start;
  }

  .container-intro-sections {
    width: 100%;
  }

  .scroll-down {
    display: none;
  }
}


.social-media-container {
  width: 10%;
  max-width: 10%;
  margin-top: 2.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 1.5em;
  gap: 1.5em;
}

.social-media-container:after {
  background: #D8A45C;
  content: "";
  height: 2rem;
  width: 2px;
}

@media only screen and (max-width: 950px) {
  .social-media-container {
    order: 2;
    flex-direction: row;
    margin-top: 1.5rem;
    justify-content: center;
    width: 100%;
    max-width: 100%;
  }

  .social-media-container:after {
    background: none;
    content:none;
    height: 0;
    width: 0;
  }
}

.content-about {
  display: flex;
  grid-gap: 5em;
  gap: 5em;
  justify-content: space-between;
  padding-left: 10%;
  padding-right: 10%;
  word-spacing: 2px;
  line-height: 1.7rem;
}

.content-about-profile {
  width: 100%;
}

.self-description {
  margin-bottom: 2rem;
  text-align: left;
  word-spacing: 4px;
  font-weight: 600;
}

.cards-container-about {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
}

/* Smaller screens */
@media only screen and (max-width: 950px) {
  .content-about {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.section-my-image {
 min-width: 45%;
 max-width: 100%;
 display: flex;
 justify-content: flex-start;
 align-items: center;
}

.my-image-container {
  height: 20em;
  width: 20em;
  max-width: 75%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15%;
	background: linear-gradient(-45deg, #4DB5FF, #689DF3, #8383DF, #9967C2, #A7499E, #AB2576);
	background-size: 400% 400%;
	-webkit-animation: gradient 7s ease infinite;
	        animation: gradient 7s ease infinite;
  /* padding: 0.15rem; */
}

@-webkit-keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.my-image-container img {
  height: 20em;
  width: 20em;
  object-fit: cover;
  -webkit-transform: rotate(8deg);
          transform: rotate(8deg);
  border-radius: 15%;
  transition: 0.3s;
}

.my-image-container img:hover {
  -webkit-transform: rotate(0);
          transform: rotate(0);
}

/* Smaller screens */
@media only screen and (max-width: 950px) {
  .section-my-image {
    min-width: 80%;
    justify-content: center;
   }

  .my-image-container {
    max-width: 80%;
  }
}

.section-heading {
  margin-bottom: 2rem;
}
.card {
  background:#E6F4F1;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  border: 1px solid;
  border-color:#E6F4F1;
  font-weight: 700;
  overflow: hidden;
  border-color:#D8A45C;
}

.card-one {
  padding: 2rem;
  width: 45%;
  min-width: 45%;
  align-items: center;
  text-align: center;
}

.card-one .card-link-redirect:hover {
  color: #4db5ff ;
}

.card-one .card-thumbnail,
.card-two .card-thumbnail {
  color: #9E702A;
  font-size: larger;
}

.card-two {
  padding: 3rem;
}

.card-two .title {
  text-align: center;
  margin-bottom: 1.5rem;
}

.card-two .items-list {
  display: grid;
  grid-template-columns: 1fr 1fr ;
  grid-gap: 2rem;
  gap: 2rem
}

.card-two .items-list .item {
  display: flex;
  grid-gap: 5%;
  gap: 5%;
  width: 100%;
}

.card-two .items-list .item .container-icon {
  width: 20%;
}

.card-two .items-list .item .container-skill-text{
  width: 70%;
}

.card-three {
  padding: 1.2rem;
  display: flex;
  flex-direction: column;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  width: 20rem;
  min-width: 30%;
}

.card-three .image {
  width: 100%;
  min-height: 10rem;
  max-height: 10rem;
  border-radius: 2rem;
  overflow: hidden;
 
}

.card-three .title {
  height: 3rem;
}

.card-three .text {
  min-height: 5rem;
  height: 5rem;
  font-size: 0.9em;
  word-spacing: 2px;
  line-height: 1.3rem;
}

.card-three .skills {
  display: flex;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.card-three .skills .item {
  border-right: 2px solid black;
  font-size: 80%;
  padding-right: 0.3rem;
  padding-left: 0.2rem;
}

.card-three .skills .item:last-child {
  border-right: none;
  padding-right: 0;
}

.card-three .skills .item:first-child {
  padding-left: 0;
}

.card-three .buttons {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin-top: 2rem;
  margin-bottom: 1.5rem;
}

.card-four {
  max-width: 40rem;
  min-width: 9rem;
  width: 40rem;
  min-height: 18rem;
  max-height: 18rem;
  height: 18rem;
  padding: 2rem;
  align-items: center;
  padding-top: 2.3rem;
  word-spacing: 2px;
  line-height: 1.3rem;
}

.card-four .icon {
  background:#D8A45C ;
  padding: 0.5rem;
  border-radius: 0.4rem;
}

@media only screen and (max-width: 950px) {
  .card-two {
    max-height: none;
    padding: 2rem;
  }

  .card-four {
    min-height: none;
    max-height: none;
    height: auto;
  }
}

@media only screen and (max-width: 285px) {
  .card {
    padding: 2rem;
    font-size: 0.8em;
  }

  .card h2, .card h3 {
    padding: 2rem;
    font-size: 1.2em;
  }

  .card-two .title {
    margin-bottom: 0.5rem;
  }

  .card-two .items-list {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem
  }
}


.container-cards-skills {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 5rem;
  gap: 5rem
}
@media only screen and (max-width: 950px) {
  .container-cards-skills {
    display: flex;
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
  }
}
.container-cards-projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
}
.container-cards-testimonials {
  display: flex;
  justify-content: center;
  align-items: center;

}

.container-carousel-pickers {
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

.carousel-picker {
  width: 0.6rem;
  min-width:  0.6rem;
  height:  0.6rem;
  min-height:  0.6rem;
  background: gray;
  border: 1px solid black;
  border-radius: 50%;
  padding: 0;
}

.carousel-picker-picked {
  background: purple;
}
.contact-content {
  display: flex;
  justify-content: center;
  grid-gap: 10%;
  gap: 10%;
  padding-bottom: 5rem;
}

.contact-content .card-one {
  width: 90%;
}

.container-email-card {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40%;
}

.container-contact-form {
  width: 50%;
  display: flex;
}

.container-contact-form .width-100 {
  width: 100%;
}

.container-contact-form input {
  width: 100%;
  height: 4rem;
  border-radius: 0.6rem;
  padding: 1rem;
  margin-bottom: 1rem;
  border: 2px solid #D8A45C;

}

.container-contact-form textarea:active,
.container-contact-form textarea:focus,
.container-contact-form input:active,
.container-contact-form input:focus 
{
  outline: none;

}

.container-contact-form textarea {
  width: 100%;
  height: 9rem;
  border-radius: 0.6rem;
  padding: 1rem;
  margin-bottom: 1rem;
  border: 2px solid #D8A45C;
  resize: none;
}

@media only screen and (max-width: 950px) {
  .contact-content {
    flex-direction: column;
    grid-gap: 2rem;
    gap: 2rem;
  }

  .container-email-card {
    width: 100%;
  }
  
  .container-contact-form {
    width: 100%;
  }
}

.footer {
  background: #3B4856;
  padding: 1rem;
  color: #E6F4F1;
}

.nav-footer {
  display: flex;
  justify-content: center;
  padding: 1rem;
}

.footer .social-media-container {
  /* gap: 2.4rem; */
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 100%;
}

.footer .social-media-container a {
  padding: 0.6rem;
  border: 2px solid #E6F4F1;
  border-radius: 50%;
  width: 2.4rem;
  height: 2.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer .social-media-container:after {
  display: none;
}

.footer-author {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

@media only screen and (max-width: 950px) {
  .nav-footer {
    flex-direction: column;
    align-items: center;
    padding: 0;
  }

  .nav-footer .copyright {
    order: 200;
    padding: 2rem;
  }

  .footer .social-media-container {
    grid-gap: 2.4rem;
    gap: 2.4rem;
    display: flex;
    flex-direction: row;
  }
}

.container-navMenu {
  position: fixed;
  bottom: 0;
  width: 100%;
  z-index: 3000;
  height: 6rem;
}

.transparent-effect {
  background-color: transparent;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

.container-navMenu .navMenu {
  position: absolute;
  width: 20rem;
  border-radius: 5rem;
  height: 4rem;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
  padding: 2rem;
  left: 50%;
  -webkit-transform: translateX(calc(-50%));
          transform: translateX(calc(-50%));
  overflow: hidden;
  justify-content: flex-end;
  transition: all 1s ease;
}

.container-navMenu .navMenu-close {
  left: 90%;
  width: 1rem;
  justify-content: center;
}

.container-navMenu .navMenu .nav-item {
  padding: 0.55rem;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s ease;
  color:#9E702A;
}

.container-navMenu .navMenu .nav-item:hover {
  background-color:#bcbcbc;
  border-radius: 5rem;
}

.container-navMenu .navMenu-close .nav-item-closed {
  display: none;
}

.container-navMenu .navMenu .hide-menu {
  padding: 0;
  border: 0;
  margin: 0;
  font-size: 1.3em;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #78818d;
}

.container-navMenu .navMenu .hide-menu:hover {
  background: none;
}

